[data-template-block-id="iconic-was-section"] {
	.wp-block-woocommerce-product-section-header__heading {
		margin-bottom: 10px;
	}
}

.wp-block-iconic-was-product-block-editor {
	.wp-block-iconic-was-product-block-editor__field {
		margin-bottom: 24px;
	}

	.wp-block-iconic-was-product-block-editor__field-dimensions {

		.components-text {
			font-size: 11px;
			font-weight: 500;
			line-height: 1.4;
			text-transform: uppercase;
			box-sizing: border-box;
			display: block;
			padding-top: 0px;
			padding-bottom: 0px;
			max-width: 100%;
			z-index: 1;
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
		}

		& > .components-text {
			margin-bottom: 8px;
		}

		.components-input-control__input {
			max-width: 80px;
		}
	}

	.woocommerce-collapsible-content__toggle {
		span {
			font-size: 14px;
			font-weight: 600;
			margin-left: 16px;
		}
	}

	.woocommerce-collapsible-content__content {
		margin-left: 16px;
	}

	.wp-block-iconic-was-product-block-editor__fees {
		margin-top: 10px;
		
		.woocommerce-collapsible-content__content {
			margin-top: 16px !important;
    		margin-bottom: 32px;
		}
	}

	.wp-block-iconic-was-product-block-editor__image-wrapper {
		+ .wp-block-iconic-was-product-block-editor__image-wrapper {
			margin-top: 10px;
		}
	}

	.wp-block-iconic-was-product-block-editor__image-label {
		font-weight: 600;
		margin-bottom: 10px;
	}

	.wp-block-iconic-was-product-block-editor__image-preview {
		display: inline-block;
		position: relative;
		border: 1px solid #ddd;
		width: 50px;
		height: 50px;
		background-color: #ddd;

		img {
			width: 50px;
			height: 50px;
		}
	}

	.wp-block-iconic-was-product-block-editor__image-preview--bg-transparent {
		background-color: transparent;
	}

	.wp-block-iconic-was-product-block-editor__image-icon {
		position: absolute;
		right: -15px;
		cursor: pointer;
		background: #fff;
		border-radius: 50%;
		box-shadow: 0 0 2px 1px rgba(0, 0, 0, .2);
		top: -10px;

		+ .wp-block-iconic-was-product-block-editor__image-icon {
			top: 15px;
		}
	}

	.wp-block-iconic-was-product-block-editor__color-label {
		font-weight: 600;
	}

	.wp-block-iconic-was-product-block-editor__color-indicator {
		border-radius: 0.375rem;
		display: block;
		width: 30px;
		height: 30px;
		margin-bottom: 10px;
	}
}